.msg-modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  color: black;
  width: 80%;
  height: 300px;
  width: 300px;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  button{
      width: 100px;
      height: 30px;
      padding: 5px;
      border-radius: 5px;
      margin-top:5px;
  }
}